import React,{useEffect,useState} from 'react';

export const TodoApp=()=>{

			const [task,setTask]=useState([]);
			const [newTask,setNewTask]=useState("");

			
			useEffect(()=>{
				if(JSON.parse(localStorage.getItem("tasks")).tasks.length > 0 ){
					checkIfThereAreTask();
					
				}
			},[]);

			useEffect(()=>{
				localStorage.setItem("tasks",JSON.stringify({tasks:task}));
			},[task]);

			function checkIfThereAreTask(){
				let lsTasks=JSON.parse(localStorage.getItem("tasks"));
				setTask([...lsTasks.tasks]);
			}

			function addTask(e){
				e.preventDefault();
				setTask([...task, newTask]);
				setNewTask("");
			}

			function deleteTask(idTask){
				let taskDone=document.getElementById(idTask);
				const tasks=task;
				taskDone.style="text-decoration:line-through;";

				setTimeout(()=>{

					const dropTask=tasks.filter((t, i)=>{
						return i !== idTask
					});
					setTask([...dropTask]);
				},500);
			}

            return(
                <div className="app">
						<div id="container">
							<h1>Lista de tareas</h1>
							
							<form onSubmit={addTask}>
								<input className="taskIn" type="text" name="todos" placeholder="nueva tarea..." maxLength="200" 
								value={newTask} 
								required
								onChange={e=>{
									setNewTask(e.target.value);
								}}/>
								<input type="submit" className="btn" value="agregar"/>
							</form>
							<ul>
								{
									(task.length > 0) 
									&& task.map((task,i)=>{
										return(
											<li key={i}>
												<input type="text" id={i} value={task} readOnly
												style={{width:'80%'}}/>
												<button className="delete"
												onClick={()=>{
													deleteTask(i)
												}}>X</button>
											</li>
										);
									})
								}
							</ul>
						</div>
                </div>
            );
}